    for (let i = 1; i < 31; i++) {
      self.times.push({
        label: i > 1 ? i + " times" : i + " time",
        value: i,
      });
    }

    for (let i = 1; i < 7; i++) {
      self.months.push({
        label: i > 1 ? i + " months" : i + " month",
        value: i,
      });
    }
  },
  methods: {
    getInt(value) {
      return parseInt(value);
    },
    getHoursStr(hour) {
      return hour + " hr(s)";
    },
    initList(type) {
      let self = this;
      switch (type) {
        case 0:
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 1:
          self.showCancelationHours = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 2:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 3:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 4:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupMonths = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 5:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupPeriodMonths = false;
          break;
        case 5:
          self.showCancelationHours = false;
          self.showCancelationTimes = false;
          self.showCancelationMonths = false;
          self.showMakeupHours = false;
          self.showMakeupTimes = false;
          self.showMakeupMonths = false;
          break;
      }
    },
    select(data) {
      let self = this;

      if (self.showCancelationHours) {
        self.cancelation.hours = data.value;
      }

      if (self.showCancelationTimes) {
        self.cancelation.times = data.value;
      }

      if (self.showCancelationMonths) {
        self.cancelation.months = data.value;
      }

      if (self.showMakeupHours) {
        self.makeup.hours = data.value;
      }

      if (self.showMakeupTimes) {
        self.makeup.times = data.value;
      }

      if (self.showMakeupMonths) {
        self.makeup.months = data.value;
      }

      if (self.showMakeupPeriodMonths) {
        self.makeupPeriodMonths = data.value;
      }
    },
  },
};
</script>
